/* - */
export const vantMixin = {
  data() {
    return {

    }
  },
  methods: {
    /************************************************************************/
    $textShow: function(obj, callback = '') {
      var key = this.$baseIsTypeof(obj);
      if (key == 'obj') {
        var {
          text = '', time = 1500, icon = '', overlay = false, forbidClick = true
        } = obj;
      } else {
        var newObj = {
          text: obj || '',
        };
        var {
          text = '', time = 1500, icon = '', overlay = false, forbidClick = true
        } = newObj;
      };
      return this.$toast({
        message: text, //
        icon: icon, //ico-Name
        duration: time, //
        type: 'html', //
        overlay: overlay, //
        forbidClick: forbidClick, //
        onClose: callback, //
      });
    },
    $textHid: function(el) {
      if (el) {
        el.clear(); //
      } else {
        this.$toast.clear(); //
      }
    },
    //
    $textThen: function(obj, callback = '') {
      var key = this.$baseIsTypeof(obj);
      if (key == 'obj') {
        var {
          text = '', time = 1500, icon = '', overlay = false, forbidClick = true
        } = obj;
      } else {
        var newObj = {
          text: obj || '',
        };
        var {
          text = '', time = 1500, icon = '', overlay = false, forbidClick = true
        } = newObj;
      };
      return this.$toast({
        message: text, //
        icon: icon, //ico-Name
        duration: time, //
        type: 'success', //-
        overlay: overlay, //
        forbidClick: forbidClick, //
        onClose: callback, //
      });
    },
    //
    $textCatch: function(obj, callback = '') {
      var key = this.$baseIsTypeof(obj);
      if (key == 'obj') {
        var {
          text = '', time = 1500, icon = '', overlay = false, forbidClick = true
        } = obj;
      } else {
        var newObj = {
          text: obj || '',
        };
        var {
          text = '', time = 1500, icon = '', overlay = false, forbidClick = true
        } = newObj;
      };
      return this.$toast({
        message: text, //
        icon: icon, //ico-Name
        duration: time, //
        type: 'fail', //-
        overlay: overlay, //
        forbidClick: forbidClick, //
        onClose: callback, //
      });
    },
    //
    $textLoading: function(obj) {
      var key = this.$baseIsTypeof(obj);
      if (key == 'obj') {
        var {
          text = '...', time = 0, icon = '', overlay = false, forbidClick = true, loadingType = 'circular'
        } = obj;
      } else {
        var newObj = {
          text: obj || '...',
        };
        var {
          text = '...', time = 0, icon = '', overlay = false, forbidClick = true, loadingType = 'circular'
        } = newObj;
      };
      return this.$toast({
        message: text, //
        icon: icon, //ico-Name
        duration: time, //
        loadingType: loadingType, //,  circularspinner
        type: 'loading', //-
        overlay: overlay, //
        forbidClick: forbidClick, //
      });
    },
    // 
    $textSetTime: function(callback, time) {
      var text = ' ' + time + ' ';
      const toastObj = this.$textLoading(text);
      var second = time;
      const timer = setInterval(() => {
        second--;
        if (second) {
          toastObj.message = ` ${second} `;
        } else {
          clearInterval(timer);
          //  Toast
          toastObj.clear();
          callback(true)
        }
      }, 1000);
    },
    /* **************************************** *******************************************************/
    //
    textLoading: function(callback, obj) {
      var {
        text = '...', time = 0, icon = '', overlay = false, forbidClick = true, loadingType = 'circular'
      } = obj;
      //console.log(overlay);
      var textObj = this.$textLoading(text, time, icon, overlay, forbidClick, loadingType);
      callback(textObj)
    },
    /* */
    $msgThenCatch: function(obj, callback1 = '', callback2 = () => {}) {
      var key = this.$baseIsTypeof(obj);
      if (key == 'obj') {
        var {
          title = '', msg = '', thenText = '', catchText = ''
        } = obj;
      } else {
        var newObj = {
          msg: obj || '',
        };
        var {
          title = '', msg = '', thenText = '', catchText = ''
        } = newObj;
      };
      this.$dialog.confirm({
        title: title, //
        message: msg, //
        confirmButtonText: thenText, //
        showCancelButton: true, //
        cancelButtonText: catchText
      }).then(callback1).catch(callback2);
    },
  }
}



